@import url(https://fonts.googleapis.com/css?family=Open+Sans:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font-family: 'Lato', sans-serif;
}

h1{ text-transform: uppercase; }

.Container {
  height: 100vh;
}


.main-nav{
  position: absolute;
  background-color: transparent;
  width: 100%;
  font-weight: bold;
}

.background {
  height: 100vh;
  background: -webkit-gradient(linear,left top, left bottom,from(rgba(0,0,0,.7)),to(rgba(0,0,0,.7))), url(/static/media/hero.6923d550.jpg);
  background: linear-gradient(rgba(0,0,0,.7),rgba(0,0,0,.7)), url(/static/media/hero.6923d550.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
}

.background h1 {
  text-transform: uppercase;
  font-weight: 300;
  color: #ffffff;
  font-family: 'Lato', sans-serif;
  word-spacing: 4px;
  letter-spacing: 1px;
  font-size: 300%;
}

.btn {
  margin-right: 10px;
  background-color: #e67e22;
  border: 1px solid #e67e22;
  -webkit-transition: background-color .2s, border .2s;
  transition: background-color .2s, border .2s;
  font-size: 1.2em;
  font-weight: 300;
  padding: 10px 30px;
}

.btn.btn-ghost{
  background-color: transparent;
  color: #e67e22;
  font-weight: 300;
}

.btn:hover {
  background-color: #bd702d;
  border: 1px solid #bd702d;
  color: #ffffff;
}
.list-group-item{
  padding: 20px;
  text-decoration: none;
  border: none;
  background: transparent;
}

.list-group-item a{
  font-family: 'Lato', sans-serif;
  font-size: 18px;
  text-transform: uppercase;
  color:#ffffff;
  padding: 4px;
  text-decoration: none;
  -webkit-transition: border-bottom 0.1s;
  transition: border-bottom 0.1s;
}

.list-group-item a:hover{
  border-bottom: 2px solid #bd702d;
}
/* Underline Trick */
.underline::after{
  display: block;
    height: 2px;
    background-color: #e67e22;
    content: " ";
    width: 100px;
    margin: 30px auto 0;
}

/* Food Delivery Section */
.FoodDelivery{
  height: 75vh;
}

.card {
  border: none;
  font-weight: 300;
  /* font-size: 20px; */
}
/* Food Gallery */
div.card{
  overflow: hidden;
}

img.Image{
  color: black;
}
